import React from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Footer from "./components/Footer";
import Navigation from "./components/Navigation";
import HomePage from "./pages/HomePage";
import AboutPage from "./pages/AboutPage";
import PortfolioPage from "./pages/PortfolioPage";
import ContactPage from "./pages/ContactPage";
import ResumePage from "./pages/ResumePage";
import NoMatch from './pages/NoMatch';

function App() {
  return (
    <div>
      <Navigation />
      <BrowserRouter>
        <Routes>
          <Route exact path="/my-portfolio" element={<HomePage />} />
            <Route index element={<HomePage />} />
            <Route path="/my-portfolio/about" element={<AboutPage />} />
            <Route path="/my-portfolio/portfolio" element={<PortfolioPage />} />
            <Route path="/my-portfolio/resume" element={<ResumePage />} />
            <Route path="/my-portfolio/contact" element={<ContactPage />} />
            <Route component={NoMatch} />
        </Routes>
      </BrowserRouter>
      <Footer />
    </div>
  );
}

export default App;
